import type { MarkdownToHTMLRule } from '@ali/4ever-cangjie';
import { getEmojiData } from '../../utils/getEmojiData';

const rule: MarkdownToHTMLRule = {
  uses: [{
    plugin: () => import('markdown-it-emoji'),
    data: async () => {
      return [{
        defs: await getEmojiData(),
      }];
    },
  }],
};

export default rule;
